<!-- 合同详情 -->
<template>
	<view class="details">
		<view class="group">
			<u-cell-group>
				<u-cell title="合同编号" :value="details.contractNumber"></u-cell>
				<u-cell title="合同类别" :value="details.contractType == 0 ? '采购': '销售'"></u-cell>
				<u-cell title="合同名称" :value="details.contractName"></u-cell>
				<u-cell title="合同标的品种" :value="details.contractSort"></u-cell>
				<u-cell title="合同单价" :value="details.contractPrice"></u-cell>
				<u-cell title="合同标的金额(万元)" :value="details.contractMoney"></u-cell>
				<u-cell title="签约单位" :value="details.signingUnit"></u-cell>
				<u-cell title="项目签订时间" :value="details.signingTime"></u-cell>
				<u-cell title="合同生效时间" :value="details.startTime"></u-cell>
				<u-cell title="约定入库/提货时间" :value="details.agreeTime"></u-cell>
				<u-cell title="履约情况" :value="details.performanceState == 0 ? '正常': '违约'"></u-cell>
				<u-cell title="对应项目名称" :value="details.projectName"></u-cell>
			</u-cell-group>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				details: {},
			};
		},
		onLoad({
			params
		}) {
			this.details = JSON.parse(params)
		},
		methods: {}
	}
</script>

<style lang="scss">
	.details {
		background: #f0f3fb;
		height: 100vh;

		.group {
			margin-bottom: 24rpx;
			background: #fff;
		}
	}
</style>